.addcard{
    width: 100%;
}

.editable_display{
    background-color: #f8f8f8;
    border-radius: 3px;
    padding: 10px;
    text-align: center;
    cursor: default;
}

.editable_display:hover{
    background-color: #ccc;
}

.editable_edit{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.editable_edit input{
    border-radius: 3px;
    outline: none;
    border: 1px solid rgb(65, 62, 228);
    padding: 10px;
    font-size: 1rem;
}
.editable_edit_footer{
    display: flex;
    flex-direction: row;
    gap: 15px;
    align-items: center;
}
.editable_edit_footer svg{
    cursor: pointer;
}
.editable_display{
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 1px 2px 0px 1px rgba(0, 0, 0, 0.15);
}
.editable_display:hover{
    box-shadow: 1px 2px 0px 1px #ccc;
}

